import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import './NonMemberConfirmation.css';

export default function NonMemberConfirmation() {
  const location = useLocation();
  const navigate = useNavigate();
  const { id, nom, prenom, dateNaissance, tarif, methodePaiement } = location.state || {};
  
  const [paymentStatus, setPaymentStatus] = useState(false);
  
  // Formatage de la date pour l'affichage
  const formatDate = (dateString) => {
    const options = { day: '2-digit', month: '2-digit', year: 'numeric' };
    return new Date(dateString).toLocaleDateString('fr-FR', options);
  };
  
  // Vérifier périodiquement si le paiement a été validé
  useEffect(() => {
    if (!id) return;
    
    // Fonction pour vérifier le statut du paiement
    const checkPaymentStatus = async () => {
      try {
        const response = await axios.get(`http://localhost:4000/non-members/${id}`);
        if (response.data.success && response.data.nonMember.paid) {
          setPaymentStatus(true);
          // Rediriger vers la page d'accueil après 3 secondes
          setTimeout(() => {
            navigate('/');
          }, 3000);
        }
      } catch (error) {
        console.error("Erreur lors de la vérification du paiement:", error);
      }
    };
    
    // Vérifier immédiatement puis toutes les 5 secondes
    checkPaymentStatus();
    const intervalId = setInterval(checkPaymentStatus, 5000);
    
    // Nettoyer l'intervalle lors du démontage du composant
    return () => clearInterval(intervalId);
  }, [id, navigate]);

  // Si les données ne sont pas disponibles, rediriger vers le formulaire
  if (!nom || !prenom || !dateNaissance) {
    navigate('/non-member');
    return null;
  }

  return (
    <div className="confirmation-container">
      <div className="confirmation-card">
        {paymentStatus ? (
          <>
            <h2 className="success-title">Paiement validé !</h2>
            <p>Votre paiement a été validé par un bénévole.</p>
            <p>Vous allez être redirigé vers la page d'accueil...</p>
          </>
        ) : (
          <>
            <h2>En attente de validation</h2>
            <p>Votre demande a été enregistrée et est en attente de validation par un bénévole.</p>
            <p className="highlight-text">Veuillez vous présenter à l'accueil pour finaliser votre paiement.</p>
          </>
        )}
        
        <div className="info-box">
          <h3>Récapitulatif</h3>
          <div className="info-row">
            <span>Nom :</span>
            <span>{nom}</span>
          </div>
          <div className="info-row">
            <span>Prénom :</span>
            <span>{prenom}</span>
          </div>
          <div className="info-row">
            <span>Date de naissance :</span>
            <span>{formatDate(dateNaissance)}</span>
          </div>
          <div className="info-row">
            <span>Tarif :</span>
            <span>{tarif === 0 ? 'Gratuit' : `${tarif}€`}</span>
          </div>
          <div className="info-row">
            <span>Méthode de paiement :</span>
            <span>{methodePaiement}</span>
          </div>
        </div>
        
        <button 
          className="home-button"
          onClick={() => navigate('/')}
        >
          Retour à l'accueil
        </button>
      </div>
    </div>
  );
}
